@media (min-width:600px) {
	.wp-block-group {
		display: flex;
		align-items: center;
		gap: 48px;

		.wp-block-navigation {
			position: static;
			margin-top: 0;

			.wp-block-navigation__responsive-container {
				position: static;

				.wp-block-navigation__responsive-dialog {
					position: static;
				}
			}

			.wp-block-navigation__container {
				position: static;
				width: 100%;
				min-height: 52px;
				background: var(--secondary-48, rgba(250, 250, 250, 0.48));
				color: var(--white-contrast, #0A090A);
				display: flex;
				flex-direction: row;
				align-items: center;
				gap: 32px;


				.wp-block-navigation-item {
					position: static;
					background: transparent;

					&.current-menu-item {
						color: var(--purple-main, #6F76C0);
					}

					&.open-on-click .wp-block-navigation-submenu__toggle+.wp-block-navigation__submenu-icon {
						position: relative;
						width: 26px;
						height: 26px;
						display: block;
						background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"><path d="M8.46997 10.7402L12 14.2602L15.53 10.7402" stroke="%230E0D0F" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>');

						svg {
							display: none;
						}


					}
				}

				.wp-block-navigation__submenu-container {
					position: absolute;
					left: 0;
					right: 0;
					top: 100%;
					width: 100%;
					min-height: 52px;
					background: var(--secondary-48, rgba(250, 250, 250, 0.48));
					color: var(--white-contrast, #0A090A);
					display: flex;
					flex-direction: row;
					align-items: center;
					gap: 32px;
					padding-left: 275px;

					// &.has-primary-background-color {
					// 	background: var(--secondary-48, rgba(250, 250, 250, 0.48));
					// }


					.wp-block-navigation__submenu-container {
						left: 0;
						top: 100%;
					}
				}
			}

		}
	}
}